<template>
  <div class="info-check" v-if="loading">
    <Spin size="large" fix></Spin>
  </div>

  <div class="info-check" v-else>

    <!---->
    <Breadcrumb>
      <Breadcrumb-item></Breadcrumb-item>
      <Breadcrumb-item href="/user/list"></Breadcrumb-item>
      <Breadcrumb-item>{{user.name}}</Breadcrumb-item>
    </Breadcrumb>
    <!--/-->

    <!---->
    <div class="search-wrap" @keyup.enter="commoditySearch">
      <Input v-model="search" placeholder="">
      <Select v-model="select" slot="prepend" style="width: 80px">
        <Option value="number"></Option>
        <Option value="store_name"></Option>
      </Select>
      <Button slot="append" icon="ios-search" @click="commoditySearch"></Button>
      </Input>
    </div>
    <!--/-->

    <!---->
    <div class="table-action">
      <b></b>
      <Radio-group v-model="tableSize" type="button">
        <Radio label="large"></Radio>
        <Radio label="default"></Radio>
        <Radio label="small"></Radio>
      </Radio-group>
    </div>
    <div class="info-check-table">
      <Table :columns="columns" :data="userBuys" :size="tableSize"></Table>
    </div>
    <!--/-->

    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="count" :current="request.page" @on-change="changePage" show-total></Page>
      </div>
    </div>

  </div>
</template>

<style>
  .info-check {
    position: relative;
    height: 100%;
    width: 100%;
    padding: 15px;
    display: flex;
    flex-direction: column;
  }

  .info-check-table {
    width: 100%;
    clear: both;
    flex: 1;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-right: 15px;
  }

  td.ivu-table-expanded-cell {
    padding-left: 200px;
    text-indent: 2em;
  }

  .table-action {
    margin-bottom: 20px;
  }

  .table-action b {
    font-size: 15px;
    padding-left: 10px;
    margin-right: 20px;
    font-weight: normal;
  }

  .table-action button {
    margin: 0 30px;
  }

  .ivu-table-tbody .table-price span {
    color: #ff963d;
  }

  .ivu-table-tbody .table-price span:after {
    content: '';
    display: inline-block;
    margin-left: 5px;
  }
</style>

<script>
  import expandRow from './expandUserOne.vue'
  export default {
    data() {
      return {
      	loading: true,
        user: null,
        tableSize: 'default',
        search: '',
        select: 'number',
        columns: [
          {
            title: '',
            key: 'number',
            align: 'center',
            width: 200
          },
          {
          	type: 'expand',
            width: 50,
            render: (h, params) => {
              return h(expandRow, {
                props: {
                  row: params.row
                }
              })
            }
          },
          {
            title: '',
            key: 'product_name',
            align: 'center'
          },
          {
            title: '/()',
            key: 'price',
            width: 150,
            align: 'center',
            className: 'table-price'
          },
          {
            title: '',
            key: 'store_name',
            align: 'center',
            sortable: true
          },
          {
            title: '',
            key: 'nickname',
            align: 'center'
          },
          {
            title: '',
            key: 'phone',
            align: 'center'
          },
          {
            title: '',
            key: 'createtime',
            align: 'center',
            width: 200,
            sortable: true
          }
        ],
        userBuys: [],

        count: 0,
        request: {
          page: 1,
          uid: this.$route.params.user.id
        }

      }
    },
    created(){
      this.user = this.$route.params.user
      this.getUserBuy(this.request)
    },

    methods: {

      /**
       * 
       * */
      getUserBuy(data){
        this.$http.get(this.$global.url + 'web/orders', {
          params: data
        }).then(res => {
        	this.loading = false
          if ('200' === res.data.code) {
            this.userBuys = res.data.data
            this.count = res.data.count
          } else {
            this.$Message.error(res.data.msg)
          }
        }).catch(error => {
          this.$Modal.error({
            title: '',
            content: error
          })
        })
      },

      changePage(page){
      	this.request.page = page
        this.getUserBuy(this.request)
      },


      /**
       * 
       * */
      commoditySearch(){
        const tmp = {
          page: 1
        }
        tmp[this.select] = this.search
        tmp.uid = this.user.id
        this.request = tmp
        this.getUserBuy(tmp)
      },
    }
  }
</script>



